<template>
  <div class="home">
      <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide" @click="tostore">
                    <div class="con">
                        <div class="userImg"><img src="../../assets/logo.png"></div>
                        <div class="text">
                            <div class='name'>我叫王英豪</div>
                            <div>My English name is WHowe</div>
                            <div>一名前端工程师</div>
                            <h4>我的邮箱：86184893@qq.com</h4>
                        </div>
                    </div>
                    <div class="botline">﹀</div>
                   
                </div>
                <div class="swiper-slide">
                    <MrTabBar></MrTabBar>
                </div>
                <div class="swiper-slide"><MrRipple></MrRipple></div>
                <div class="swiper-slide" @click="topersonal">个人中心</div>
                <div class="swiper-slide" @click="tocart">去购物车</div>
                <div class="swiper-slide"><MrWorks></MrWorks></div>
                <div class="swiper-slide" @click="towaterfall">
                    <div class="waterfall">
                         <h3>瀑布流效果的实现</h3>
                        <div class="pic">
                         <img src="@/assets/images/waterfall/works.png">
                        </div>
                       
                    <div>
                        <h5>瀑布流，又称瀑布流式布局。是比较流行的一种网站页面布局.
                        主要有两种排列方式，左右和上下排列,上下排列css即可完成，此处为左右排列，实现思路：绝对定位。</h5>
**实现思路：**
     <p>1. 计算页面的宽度，计算出页面可放数据块的列数</p>
     <p>2. 将各个数据块的高度尺寸记入数组中（需要等所有图片加载完成，否则无法知道图片的高度）</p>
     <p>3. 用绝对定位先将页面第一行填满，因为第一行的top位置都是一样的，然后用数组记录每一列的总高度。</p>
     <p>4. 继续用绝对定位将其他数据块定位在最短的一列的位置之后然后更新该列的高度。</p>
     <p>5. 当浏览器窗口大小改变时，重新执行一次上面1-4步以重新排放</p>
     <p>6. 滚动条滚动到底部时加载新的数据进来后也是定位在最短的一列的位置之后然后更新该列的高度。</p>
     </div>
                    </div>
                </div>
            </div>
            <!-- 如果需要分页器 -->
            <!-- <div class="swiper-pagination"></div> -->
            
            <!-- 如果需要导航按钮 -->
            <!-- <div class="swiper-button-prev"></div>
            <div class="swiper-button-next"></div> -->
            
            <!-- 如果需要滚动条 -->
            <!-- <div class="swiper-scrollbar"></div> -->
        </div>
  </div>
</template>


<script>
import MrTabBar from '@/components/skillcomponent';
import MrRipple from '@/components/ripple';
import MrWorks from '@/components/works';

import Swiper from 'swiper';
import 'swiper/dist/css/swiper.min.css';
export default {
     components:{
            MrTabBar,
            MrRipple,
            MrWorks
        },
  mounted(){
    var mySwiper = new Swiper('.swiper-container', {
        pagination: {
            el: '.swiper-pagination',
        },
        slidesPerView :'auto',
        autoplay:5000,
        direction:'vertical',
        grabCursor:true,
        autoplayDisableOnInteraction:false,
        mousewheelControl:true,
        autoHeight:true,
        speed:500
    })
  },
  methods:{
     tostore(){
         this.$router.push('store')
     },
     topersonal(){
         this.$router.push('personal')
     },
     tocart(){
         this.$router.push('cart')
     },
     towaterfall(){
         this.$router.push('waterfall')
     }
  }
}
</script>
<style lang="scss" scoped>
.waterfall{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    width: 90%;
    h3{
        text-align: center;
    }
    p{
        margin-bottom: .1rem;
    }
    .pic{
        width: 3rem;
        height: 4rem;
        margin: auto;
        text-align: center;
        img{
            widows:100%;
            height:100%;
            object-fit: contain;
        }
    }
}
.swiper-container{
    width: 100vw;
    height: 100vh;
    position:relative;
    .swiper-slide{
        height: 100vh;
        margin:auto;
        overflow-y: auto;
        overflow-x: hidden;
        .con{
            position:absolute;
            top:50%;
            left:50%;
            transform:translate(-50%,-50%);
            text-align:center;
            //文字渐变
            background-image: linear-gradient(135deg, deeppink, dodgerblue);
            background-clip:text;
            -webkit-background-clip:text;
            box-decoration-break: clone; 
            -webkit-box-decoration-break: clone; 
            color: transparent;
            .text{
                line-height: .6rem;
         
            }
           h4{
            display: block;
            width: 600px;
            height: 200px;
            /*渐变背景，此处为能无缝拼接的渐变 即0~100%*/   /*linear-gradient(线性渐变)*/
            background-image: -webkit-linear-gradient(left, #3498db, #f47920 10%, #d71345 20%, #f7acbc 30%, #ffd400 40%, #3498db 50%, #f47920 60%, #d71345 70%, #f7acbc 80%, #ffd400 90%, #3498db);
            color: transparent; /*文字填充色为透明*/
            -webkit-text-fill-color: transparent;
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-background-size: 200% 100%; /*背景图片向水平方向扩大一倍，这样background-position才有移动与变化的空间*/
            background-size: 200% 100%;
            /* 动画 */
            animation: masked-animation 4s infinite linear;
            -webkit-animation: masked-animation 4s infinite linear;
            }
            @keyframes masked-animation {
                0% {
                    background-position: 0 0;   /*background-position 属性设置背景图像的起始位置。*/
                }
                100% {
                    background-position: -100% 0;
                }
            }

            @-webkit-keyframes masked-animation {
                0% {
                    background-position: 0 0;
                }
                100% {
                    background-position: -100% 0;
                }
            }
            .userImg{
                width:80px;
                height:80px;
                margin:20px auto;
                img{
                    border-radius:100%;
                    width:100%;
                    height:100%;
                    object-fit:cover;
                    background:#f7f7f7;
                }
            }
        }
        .botline{
            position:absolute;
            bottom:20px;
            left:50%;
            transform:translateX(-50%);
            height:30px;
            font-size:30px;
            animation:myline .8s infinite;
        }
    }
    
}
@keyframes myline
{
from {opacity:1;}
to {opacity:0;}
}
</style>




